.about {
  margin-bottom: 120px;

  @include vp-1199 {
    margin-bottom: 100px;
  }

  @include vp-767 {
    margin-bottom: 67px;
  }
}

.about__container {
  display: flex;
  flex-direction: column;
}

.about__top-row {
  margin-right: 106px;
  margin-bottom: 60px;
  display: flex;
  justify-content: space-between;

  @include vp-1199 {
    margin-right: auto;
    justify-content: flex-start;
    flex-direction: column;
  }

  @include vp-767 {
    margin-bottom: 40px;
  }
}

.about__col {
  &:first-child {
    width: 477px;

    @include vp-1199 {
      margin-bottom: 40px;
    }

    @include vp-767 {
      width: auto;
      max-width: 100%;
    }
  }

  &:last-of-type {
    width: 498px;

    @include vp-767 {
      width: auto;
      max-width: 100%;
    }
  }
}

.about__head {
  & h2 {
    color: $text-additional;
    margin-bottom: 43px;

    @include vp-767 {
      margin-bottom: 21px;
    }
  }

  & p {
    margin: 0;
    color: $text-dark;
    font-size: 22px;
    font-weight: 400;
    line-height: 32px;

    @include vp-1199 {
      font-size: 20px;
      line-height: 26px;
    }

    @include vp-767 {
      font-size: 16px;
      line-height: 22px;
    }
  }
}

.about__descr {
  & h3 {
    color: $text-additional;
    margin-bottom: 46px;

    @include vp-1199 {
      margin-bottom: 30px;
    }

    @include vp-767 {
      margin-bottom: 20px;
    }
  }

  & p {
    margin: 0;
    margin-bottom: 40px;
    color: $text-dark;
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;

    @include vp-1199 {
      line-height: 20px;
    }

    @include vp-767 {
      font-size: 14px;
      margin-bottom: 30px;
    }
  }
}

.about_link {
  width: 100%;
  max-width: 277px;

  @include vp-767 {
    max-width: 221px;
  }
}

.about__info {
  padding: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $color-default-additional;

  @include vp-1199 {
    padding: 40px;
  }

  @include vp-767 {
    padding: 40px 20px;
  }
}

.about__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(2, 478px);
  grid-template-rows: repeat(2, auto);
  grid-column-gap: 52px;
  grid-row-gap: 54px;

  @include vp-1199 {
    grid-template-columns: repeat(2, minmax(264px, 478px));
    grid-column-gap: 50px;
    grid-row-gap: 42px;
  }

  @include vp-767 {
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 22px;
  }
}

.about__item {
  display: flex;
  justify-content: space-between;
  padding: 30px 60px;
  border-radius: 16px;
  border: 1px solid $color-default-main;
  position: relative;

  @include vp-1199 {
    padding: 30px;
    flex-direction: column;
    justify-content: flex-start;
    row-gap: 12px;
  }

  @include vp-767 {
    width: 100%;
    padding: 24px 20px;
    row-gap: 8px;
  }

  &::before {
    position: absolute;
    content: "";
    top: -20px;
    left: -22px;
    width: 42px;
    height: 42px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $color-default-additional;
    background-repeat: no-repeat;
    background-position: 50% 40%;
    background-image: url("../../img/svg/icon-star.svg");
  }

  & b {
    max-width: 171px;
    color: $text-light;
    font-size: 26px;
    font-weight: 400;
    line-height: 30px;
    letter-spacing: 0.26px;
    text-transform: uppercase;

    @include vp-1199 {
      max-width: 100%;
    }
  }

  & p {
    max-width: 190px;
    margin: 0;
    margin-left: 20px;
    color: $text-light-additional;
    font-size: 16px;
    font-weight: 300;
    line-height: 20px;

    @include vp-1199 {
      max-width: 100%;
      margin-left: 0;
    }
  }
}
