@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Manrope";
  font-display: swap;
  src: url("../../fonts/Manrope-Light.woff2") format("woff2"), url("../../fonts/Manrope-Light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Manrope";
  font-display: swap;
  src: url("../../fonts/Manrope-Regular.woff2") format("woff2"), url("../../fonts/Manrope-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Manrope";
  font-display: swap;
  src: url("../../fonts/Manrope-Medium.woff2") format("woff2"), url("../../fonts/Manrope-Medium.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  font-family: "Manrope", "Arial", sans-serif;
  color: #484848;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #484848;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #484848;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  min-width: 320px;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  min-height: 54px;
  padding-top: 4px;
  padding-bottom: 4px;
  padding-left: 30px;
  padding-right: 4px;
  border-radius: 30px;
  border: 1px solid #316dc2;
  background-color: #ffffff;
  color: #316dc2;
  font-size: 14px;
  font-weight: 500;
  line-height: 14px;
  letter-spacing: 0.28px;
  text-transform: uppercase;
  cursor: pointer;
  transition: background-color 0.5s ease, color 0.5s ease, transform 0.5s ease;
}
@media (max-width: 767px) {
  .btn {
    font-size: 12px;
    line-height: 12px;
    letter-spacing: 0.24px;
  }
}
.btn span {
  margin-left: auto;
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background-color: #316dc2;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn:disabled {
  color: #a9afb7;
  pointer-events: none;
}
.btn:disabled span {
  background-color: #a9afb7;
  color: rgba(255, 255, 255, 0.6);
}
.btn--disabled {
  pointer-events: none;
  color: #a9afb7;
  border-color: #a9afb7;
}
.btn--disabled span {
  background-color: #a9afb7;
  color: rgba(255, 255, 255, 0.6);
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    background-color: #316dc2;
    color: #ffffff;
  }
  .btn:hover:not(.focus-visible) span {
    background-color: #ffffff;
    color: #316dc2;
    transition: background-color 0.5s ease, color 0.5s ease;
  }
  .btn:hover:not(.focus-visible) svg {
    transform: rotate(45deg);
  }
  .btn:hover:not(.focus-visible):active {
    color: rgba(255, 255, 255, 0.7);
  }
  .btn:hover:not(.focus-visible):active span {
    color: rgba(49, 109, 194, 0.7);
  }
  .btn:hover:not(.focus-visible):active svg {
    transform: rotate(45deg);
  }
}
.btn.focus-visible:focus {
  background-color: #316dc2;
  color: #ffffff;
}
.btn.focus-visible:focus span {
  background-color: #ffffff;
  color: #316dc2;
  transition: background-color 0.5s ease, color 0.5s ease;
}
.btn.focus-visible:focus svg {
  transform: rotate(45deg);
}
.btn.focus-visible:focus:active {
  color: rgba(255, 255, 255, 0.7);
}
.btn.focus-visible:focus:active span {
  color: rgba(49, 109, 194, 0.7);
}
.btn.focus-visible:focus:active svg {
  transform: rotate(45deg);
}

section h2 {
  margin: 0;
  font-size: 50px;
  font-weight: 400;
  line-height: 50px;
}
@media (max-width: 1199px) {
  section h2 {
    font-size: 34px;
    line-height: 50px;
  }
}
@media (max-width: 767px) {
  section h2 {
    font-size: 20px;
    line-height: 20px;
  }
}

section h3 {
  margin: 0;
  font-size: 32px;
  font-weight: 400;
  line-height: 36px;
}
@media (max-width: 1199px) {
  section h3 {
    font-size: 28px;
    line-height: 28px;
  }
}
@media (max-width: 767px) {
  section h3 {
    font-size: 18px;
    line-height: 22px;
  }
}

.about {
  margin-bottom: 120px;
}
@media (max-width: 1199px) {
  .about {
    margin-bottom: 100px;
  }
}
@media (max-width: 767px) {
  .about {
    margin-bottom: 67px;
  }
}

.about__container {
  display: flex;
  flex-direction: column;
}

.about__top-row {
  margin-right: 106px;
  margin-bottom: 60px;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1199px) {
  .about__top-row {
    margin-right: auto;
    justify-content: flex-start;
    flex-direction: column;
  }
}
@media (max-width: 767px) {
  .about__top-row {
    margin-bottom: 40px;
  }
}

.about__col:first-child {
  width: 477px;
}
@media (max-width: 1199px) {
  .about__col:first-child {
    margin-bottom: 40px;
  }
}
@media (max-width: 767px) {
  .about__col:first-child {
    width: auto;
    max-width: 100%;
  }
}
.about__col:last-of-type {
  width: 498px;
}
@media (max-width: 767px) {
  .about__col:last-of-type {
    width: auto;
    max-width: 100%;
  }
}

.about__head h2 {
  color: #316dc2;
  margin-bottom: 43px;
}
@media (max-width: 767px) {
  .about__head h2 {
    margin-bottom: 21px;
  }
}
.about__head p {
  margin: 0;
  color: #484848;
  font-size: 22px;
  font-weight: 400;
  line-height: 32px;
}
@media (max-width: 1199px) {
  .about__head p {
    font-size: 20px;
    line-height: 26px;
  }
}
@media (max-width: 767px) {
  .about__head p {
    font-size: 16px;
    line-height: 22px;
  }
}

.about__descr h3 {
  color: #316dc2;
  margin-bottom: 46px;
}
@media (max-width: 1199px) {
  .about__descr h3 {
    margin-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .about__descr h3 {
    margin-bottom: 20px;
  }
}
.about__descr p {
  margin: 0;
  margin-bottom: 40px;
  color: #484848;
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
}
@media (max-width: 1199px) {
  .about__descr p {
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .about__descr p {
    font-size: 14px;
    margin-bottom: 30px;
  }
}

.about_link {
  width: 100%;
  max-width: 277px;
}
@media (max-width: 767px) {
  .about_link {
    max-width: 221px;
  }
}

.about__info {
  padding: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #316dc2;
}
@media (max-width: 1199px) {
  .about__info {
    padding: 40px;
  }
}
@media (max-width: 767px) {
  .about__info {
    padding: 40px 20px;
  }
}

.about__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(2, 478px);
  grid-template-rows: repeat(2, auto);
  grid-column-gap: 52px;
  grid-row-gap: 54px;
}
@media (max-width: 1199px) {
  .about__list {
    grid-template-columns: repeat(2, minmax(264px, 478px));
    grid-column-gap: 50px;
    grid-row-gap: 42px;
  }
}
@media (max-width: 767px) {
  .about__list {
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 22px;
  }
}

.about__item {
  display: flex;
  justify-content: space-between;
  padding: 30px 60px;
  border-radius: 16px;
  border: 1px solid #f0efe9;
  position: relative;
}
@media (max-width: 1199px) {
  .about__item {
    padding: 30px;
    flex-direction: column;
    justify-content: flex-start;
    row-gap: 12px;
  }
}
@media (max-width: 767px) {
  .about__item {
    width: 100%;
    padding: 24px 20px;
    row-gap: 8px;
  }
}
.about__item::before {
  position: absolute;
  content: "";
  top: -20px;
  left: -22px;
  width: 42px;
  height: 42px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #316dc2;
  background-repeat: no-repeat;
  background-position: 50% 40%;
  background-image: url("../../img/svg/icon-star.svg");
}
.about__item b {
  max-width: 171px;
  color: #ffffff;
  font-size: 26px;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: 0.26px;
  text-transform: uppercase;
}
@media (max-width: 1199px) {
  .about__item b {
    max-width: 100%;
  }
}
.about__item p {
  max-width: 190px;
  margin: 0;
  margin-left: 20px;
  color: #f0efe9;
  font-size: 16px;
  font-weight: 300;
  line-height: 20px;
}
@media (max-width: 1199px) {
  .about__item p {
    max-width: 100%;
    margin-left: 0;
  }
}

